
export class Raw {

    private data: any;

    constructor(data: any = {}) {
        this.data = data;
    }

    public static make(data: any = {}): Raw {
        return new Raw(data);
    }

    public set(key: string, value: any): this {
        this.data[key] = value;
        return this;
    }

    public toJSON() {
        return this.data;
    }

}